<style>
	.text-blank{
		background: rgba(255,100,100,.5);
	}

	.sl_no-col{
		width:70px !important;
	}
	.species-list, .species-list input{
		font-size: .75rem;
	}
	.species-list td{
		vertical-align: middle;
	}
	.form-details{
		padding: 10px;
		background: rgba(130,170,0,.25);
	}
	.species-list{
		width: 100%;
		overflow-x: scroll ;
		padding: 10px;
		background: rgba(0,180,200,.25);
		padding-bottom: 100px;
	}
	.autocomplete{
		min-width: 200px !important;
	}
	.quality-cell{
		min-width: 300px !important;		
	}
	.required:after {
		content:" *";
		color: red;
	}

</style>
<template>
	<div class="container-fluid my-5">
		<div class="container-fluid py-2 text-center">
			<h1 class="text-secondary">Big Butterfly Month 2021 - Butterfly Counts Data Sheet</h1>
		</div>
		<div class="form-details container">
			<table class="table table-sm">
				<tbody>
					<tr>
						<td class="required">Name of the Person taking the count</td>
						<td><input class="form-control" type="text" v-model="form_values.name" :class="inputDetailsClass('name')" required></td>
					</tr>
					<tr>
						<td>Affiliation (NGO/school, etc.)</td>
						<td><input class="form-control" type="text" v-model="form_values.affiliation"></td>
					</tr>
					<tr>
						<td>Contact Number</td>
						<td><input class="form-control" type="text" v-model="form_values.phone"></td>
					</tr>
					<tr>
						<td class="required">E Mail ID</td>
						<td><input class="form-control" type="text" v-model="form_values.email" :class="inputDetailsClass('email')" required></td>
					</tr>
					<tr>
						<td>Name and/or number of team members</td>
						<td><input class="form-control" type="text" v-model="form_values.team_members"></td>
					</tr>
					<tr>
						<td>Link to photo records uploaded</td>
						<td><input class="form-control" type="text" v-model="form_values.photo_link"></td>
					</tr>
					<tr>
						<td  class="required">Location (Place name, nearest village/town)</td>
						<td><input class="form-control" type="text" v-model="form_values.location" :class="inputDetailsClass('location')" required></td>
					</tr>
					<tr>
						<td class="required">State</td>
						<td>
							<autocomplete :value="form_values.state" v-model="form_values.state" :items="states" :class="inputDetailsClass('state')"/>
						</td>
					</tr>
					<tr>
						<td>GPS Coordinates (from phone)</td>
						<td class="d-flex">
							<input class="form-control" type="text" v-model="form_values.coordinates">
							<button class="btn btn-primary btn-sm mx-auto" @click='getCoordinates()'>Get current coordinates</button>
						</td>
					</tr>
					<tr>
						<td class="required">Date</td>
						<td><input class="form-control" type="date" v-model="form_values.date"  :class="inputDetailsClass('date')" required></td>
					</tr>
					<tr>
						<td>Start Time</td>
						<td><input class="form-control" type="time" v-model="form_values.start_time"></td>
					</tr>
					<tr>
						<td>End Time</td>
						<td><input class="form-control" type="time" v-model="form_values.end_time"></td>
					</tr>
					<tr>
						<td>Altitude, m</td>
						<td><input class="form-control" type="text" v-model="form_values.altitude"></td>
					</tr>
					<tr>
						<td>Total distance covered, km (approx. estimated)</td>
						<td><input class="form-control" type="text" v-model="form_values.distance"></td>
					</tr>
					<tr>
						<td>Weather (sunny, clouded, windy, etc.)</td>
						<td><input class="form-control" type="text" v-model="form_values.weather"></td>
					</tr>
					<tr>
						<td>Comments</td>
						<td><input class="form-control" type="text" v-model="form_values.comments"></td>
					</tr>
				</tbody>
			</table>
		</div>

		<div class="species-list">
			<table class="table table-sm">
				<thead class="text-center">
					<tr>
						<th class="sl_no-col">Sl. No</th>
						<th class="text-nowrap">Common Name</th>
						<th class="text-nowrap">Scientific Name</th>
						<th class="text-nowrap">No of Individuals</th>
						<th class="text-nowrap quality-cell">Remarks</th>
						<th class="text-nowrap quality-cell">Data Quality Notes</th>
						<th class="text-nowrap"></th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="(row, row_id) in row_values" :key="row_id">
						<td v-for='col in row_cols'>
							<autocomplete v-bind:value="row_values[row_id][col[0]]" :items="common_names" v-if="col[0]=='common_name'" :class="inputSpeciesClass(row, col[0])" @input="autocomplete_result([row.sl_no, col[0]],...arguments)" />
							<autocomplete v-bind:value="row_values[row_id][col[0]]" :items="scientific_names" v-else-if="col[0]=='scientific_name'" :class="inputSpeciesClass(row, col[0])" @input="autocomplete_result([row.sl_no, col[0]],...arguments)"/>
							<input :type="col[2]" v-model="row_values[row_id][col[0]]" class="form-control" :class="inputSpeciesClass(row, col[0])" v-else>
						</td>
						<td>
							<ul v-if="row_comments[row.sl_no]" class="m-0 text-danger">
								<li v-for="c in row_comments[row.sl_no]" v-text="c"></li>
							</ul>
						</td>
						<td>
							<button class="btn btn-sm btn-danger" @click='removeRow(row.sl_no)'>Remove</button>
						</td>
					</tr>
				</tbody>
			</table>			
		</div>
		<div class="row d-flex justify-content-center mb-5">
			<button class="btn btn-success btn-block w-25" @click="addSpeciesRow()" title="Add another species">+</button>
		</div>
		<div class="row d-flex justify-content-center">
			<button class="btn btn-success btn-block" @click="submitData()">Submit Count Sheet</button>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	import synoptic from './synoptic.json'
	import boi from './boi.json'
	import Autocomplete from './Autocomplete.vue';

	export default {
		name:"butterfly-count",
		components: { Autocomplete },
		data(){
			return {
				form_values:{name:"",affiliation:"",phone:"",email:"",team_members:"",photo_link:"",location:"",state:"",coordinates:"",date:"",start_time:"",end_time:"",altitude:"",distance:"",weather:"",comments:"",},
				row_values:[],
				row_cols:[
					["sl_no", "Sl No", "number"],
		            ["common_name", "Common Name", "text"],
		            ["scientific_name", "Binomial Name (if known)", "text"],
		            ["individuals", "No. of individuals", "number"],
		            ["remarks", "Remarks (Male/Female/seasonal form, etc.)", "text"]
				],
				states:["Andaman & Nicobar","Andhra Pradesh","Arunachal Pradesh","Assam","Bihar","Chandigarh","Chhattisgarh","Dadra & Nagar Haveli and Daman & Diu","Delhi","Goa","Gujarat","Haryana","Himachal Pradesh","Jammu & Kashmir","Jharkhand","Karnataka","Kerala","Ladakh","Lakshadweep","Madhya Pradesh","Maharashtra","Manipur","Meghalaya","Mizoram","Nagaland","Odisha","Puducherry","Punjab","Rajasthan","Sikkim","Tamil Nadu","Telangana","Tripura","Uttar Pradesh","Uttarakhand","West Bengal"],
				synoptic_names: synoptic,
				boi_names: boi,
				row_comments:{},
				last_row_id:0,
				common_names:[],
				scientific_names:[],
				local_storage_fields:["name","affiliation","phone","email","team_members","state"],
			}
		},
		computed:{
		},
		watch: {
		},
		created(){
			this.init();
		},
		methods:{
			init(){
				this.getStoredValues();
				this.addSpeciesRow();
				this.populateAutocompleteLists();
				this.populateRowComments();
			},
			getStoredValues(){
				this.local_storage_fields.forEach(f => {
					if(window.localStorage.getItem(f) != null){
						this.form_values[f] = window.localStorage.getItem(f);
					}
				})
			},
			setStoredValues(){
				this.local_storage_fields.forEach(f => {
					if(this.form_values[f] != undefined){
						window.localStorage.setItem(f, this.form_values[f]);
					}
				})
			},
			getCoordinates(){
				if (navigator.geolocation) {
					navigator.geolocation.getCurrentPosition(this.showPosition);
				} else {
					x.innerHTML = "Geolocation is not supported by this browser.";
				}
			},
			showPosition(position) {
				this.form_values.coordinates  = `${position.coords.latitude}N, ${position.coords.longitude}E`;
			},
			addSpeciesRow(){
				let op = {};
				this.row_cols.forEach(c => {
					if(c[0] == "sl_no"){
						op[c[0]] = this.last_row_id + 1;
					} else if(c[0] == "individuals"){
						op[c[0]] = 1;
					}
					else {
						op[c[0]] = '';
					}
				})
				this.$set(this.row_values,this.last_row_id,op)
				this.last_row_id++;
				this.populateRowComments();
			},
			autocomplete_result(ele, result){
				this.row_values[ele[0] - 1][ele[1]] = result;

				this.populateRowComments();
			},
			populateAutocompleteLists(){
				this.synoptic_names.forEach(sn => {
					this.scientific_names.push(sn[0])
					this.common_names.push(sn[1])
				})
			},
			populateRowComments(){
				this.row_comments = {};
				this.row_values.forEach(row => {
					console.log(row);
					this.row_comments[row.sl_no] = [];
					this.row_cols.forEach(col => {
						let cell_class = this.inputSpeciesClass(row,col[0]);

						switch(cell_class){
							case "text-blank": this.row_comments[row.sl_no].push(this.titleCase(col[0]) + " is blank. Please fill it if possible");
										break;
							case "unmatched-name":
							case "partially-matched-name":
											if(row[col[0]] == ""){
												this.row_comments[row.sl_no].push(this.titleCase(col[0]) + " not entered.")
											} else {
												this.row_comments[row.sl_no].push(this.titleCase(col[0]) + " doesn't match with our lists.")
											}
							 // Please check the name. If you are unsure, go ahead and validate it, we will review this in a short while.
						}
					})
				})
			},
			inputSpeciesClass(row, col){
				let op = "";
				let row_id = this.row_values.indexOf(row);
				switch(col){
					case "sl_no":
					case "individuals": 
											if((row[col] == "") || (row[col] == null)){
												op = "text-blank"
											}
											break;
					case "scientific_name": op = "unmatched-name";
										this.synoptic_names.forEach(sp => {
											if(sp[0] == row[col] && row[col] != ""){
												op = "matched-name";
												this.row_values[row_id]['common_name'] = sp[1];
											}
										})
										if(op == "unmatched-name" && row["common_name"] != ""){
											op = "partially-matched-name"
										}
										break;
					case "common_name": op = "unmatched-name";
										this.synoptic_names.forEach(sp => {
											if(sp[1] == row[col] && row[col] != ""){
												op = "matched-name";
												this.row_values[row_id]['scientific_name'] = sp[0];
											}
										})
										if(op == "unmatched-name" && row["scientific_name"] != "" ){
											op = "partially-matched-name"
										}
										break;
				}
				return op;
			},
			inputDetailsClass(col){
				let op = "";
				if((this.form_values[col] == "") || (this.form_values[col] == null))
					if(col == 'state'){
						op = "auto-text-blank";
					} else {
						op = "text-blank";
					}
				return op;
			},
			titleCase(str) {
				let splitStr = str.toLowerCase().split('_');
				for (let i = 0; i < splitStr.length; i++) {
					splitStr[i] = splitStr[i].charAt(0).toUpperCase() + splitStr[i].substring(1);
				}
				return splitStr.join(' ');
			},
			removeRow(row_id){
				let row_index = -1;
				this.row_values.forEach((r,id) => {
					if(r.id == row_id){
						row_index = id;
					}
				})
				if (row_index > -1) {
					this.row_values.splice(row_index, 1);
				}
				this.row_values.forEach((row, row_id) => {
					if(this.row_values[row_id].sl_no != row_id + 1 ){
						this.row_values[row_id].sl_no = row_id + 1
						this.row_values[row_id].sl_no = row_id + 1
						this.last_row_id--;
					}
				})
			},
			submitData(){
				let post_data = {
					form:this.form_values,
					rows:this.row_values
				}
				let required_fields = ["name","email","location","date", "state"];
				let required_set = true;
				required_fields.forEach(f => {
					if(this.form_values[f] == "")
						required_set = false;
				})
				if(!required_set){
					alert("Error! required field not filled, please fill all the fields marked in red");
				} else {
					this.setStoredValues();

					const axios = require('axios');

					axios.post('/butterfly_count/pwa_post', post_data)
						.then(function (response) {
							if(response.status == 200){
								console.log(response)
								// window.open("/butterfly_count", "_self");
							}
						})
						.catch(function (error) {
							console.log(error);
						});

					alert("Count Sheet Submitted Successfully")
				}
			}
		}
	};
</script>
